import React from 'react';
import StoreItems from '../components/StoreItems';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';

// temp function to mimic database API call 
import { VendingItem } from '../assets/util';

interface StoreProps {
  addToCart: (key: number) => void;
  deleteFromCart: (key: number) => void;
  items: [VendingItem] | null;
}

const Store: React.FC<StoreProps> = (props) => {

  const history = useHistory();

  const goToCheckout = () => {
    history.push("/checkout");
  }

  const itemsList = props.items? props.items.map((value, index) =>
    <StoreItems key={index} addToCart={props.addToCart} deleteFromCart={props.deleteFromCart} item={value} />)
    : (
      <div style={{textAlign: "center"}}>Unable to load store items</div>
    );
  return (
    <div>
      <div></div>
      <div style={{ marginTop: "60px", textAlign: "left" }}>
        {itemsList}
      </div>
      <div style={{ padding: "10px" }}>
        <Button onClick={goToCheckout}>Show Cart</Button>
      </div>
    </div>
  );
}

export default Store;